.punch-button {
  padding: 10px 20px;
  position: relative;
  margin: 0 auto;
  margin-top: 85px;
  color:rgb(249, 249, 239);
  font-size: large;
  font-weight: 600;
  border-radius: 12px;
  border: 1px solid;
  background-color: #4682b4;
  display: block;
  text-align: center;
}
.clockout {
    color: rgb(249, 249, 239);
    font-size: 16px;
    font-weight: 700;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    position: relative;
    margin-top: 35px;
    margin-left: 15%;
    border: none;
    background-color: #4682b4;
  }
.clockin {
    color: rgb(249, 249, 239);
    font-size: 16px;
    font-weight: 700;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    position: relative;
    border: none;
    background-color: #4682b4;
  }
.clockin-clockout {
  position: relative;
  margin-top: 25px;
  margin-bottom: 0;
  padding: 0 0 100% 0;
  display: block;
  text-align: center;
  background-color: aliceblue;
  border-radius: 5% 5% 0 0;
}
  .status {
    position: relative;
    margin-top: 25px;
    text-align: center;
    font-weight: 600;
    display: block;

  }
  